import React from 'react';
import { Helmet } from 'react-helmet';
import { node, string } from 'prop-types';
import useSiteMetadata from '../util/useSiteMetadata';
import Navigation from '../components/Navigation';
import InPageNavigation from '../components/InPageNavigation';

/**
 * Import assets
 */
import '../css-imports';
import favicon from '../../static/favicon.ico';

/**
 * Common page layout
 *
 * Wraps a page with shared elements like navigation and sidebar, sets head
 * elements and provides an application context.
 */
const Layout = ({children, title, introtext, illustration}) => {
    const site = useSiteMetadata();

    return (<>
        <Helmet
            titleTemplate={`%s — ${site.title}`}
            defaultTitle={site.title}
        >
            <html lang="no" />
            <title>{title}</title>
            <link rel="shortcut icon" href={favicon}></link>
        </Helmet>

      <div className="sb1ds-wave">
        <div className="sb1ds-layout">
            <Navigation/>

            <main className='ffe-body-text sb1ds-main'>
                <div className="sb1ds-intro">
                    <h1 className="ffe-h1 sb1ds-intro__heading">
                        {title}
                    </h1>
                    {introtext &&
                        <p className="ffe-lead-paragraph sb1ds-intro__paragraph">
                            {introtext}
                        </p>
                    }
                    {illustration &&
                        <div className="sb1ds-intro__illustration" role="presentation">
                            <img src={`/img/${illustration}`} alt="Illustrasjon" />
                        </div>
                    }
                </div>
                {children}
            </main>

            <InPageNavigation />
        </div>
      </div>
    </>);
};

Layout.propTypes = {
    // page title, for headline and document title
    title: string.isRequired,
    // page content
    children: node.isRequired,
    introtext: string,
    illustration: string
};

export default Layout;
