import React, { useState  } from 'react';
import { string, bool, node, instanceOf  } from 'prop-types';
import { Tab as SB1Tab, TabGroup, TabButtonGroup, TabButton } from '@sb1/ffe-tabs-react';

const Tab = ({ children }) => (<>{ children }</>);

Tab.propTypes = {
    title: string.isRequired,
    children: node
};


const Tabs = ({ buttonMode = false, children }) => {
    const [activeTab, setActiveTab] = useState(0);
    const items = React.Children.map(children, (child, i) => ({i, title: child.props.title, child }));

    const TabGroupComponent = buttonMode ? TabButtonGroup : TabGroup;
    const TabItemCompoent = buttonMode ? TabButton : SB1Tab;

    return (
        <>
        <TabGroupComponent>
            { items.map(item =>
            <TabItemCompoent 
                key={item.i}
                selected={activeTab === item.i}
                onClick={() => { setActiveTab(item.i); }}
                aria-controls={'tab_' + item.i}>{item.title}
            </TabItemCompoent>
            )}
        </TabGroupComponent>
        { items.find(e => e.i === activeTab)?.child}
        </>
    );
};

Tabs.propTypes = {
    buttonMode: bool,
    children: instanceOf(Tab)
};


export { Tab, Tabs };
